const app = document.getElementById("app");
const startButton = document.querySelector("button");
const resetButton = document.querySelector("#reset");

const arr = [];

for (let i = 1; i <= 500; i++) {
  const rect = document.createElement("div");
  rect.id = i.toString();
  rect.style.width = "5px";
  rect.style.height = `${5 * i}px`;
  rect.style.backgroundColor = "white";
  rect.style.position = "absolute";
  rect.style.left = `${5 * i - 5}px`;
  rect.style.top = `${200 - 5 * i}px`;
  app.append(rect);
  arr.push(rect);
}

function start() {
  for (let i = 1; i <= arr.length; i++) {
    const num1 = Math.floor(Math.random() * arr.length);
    let num2 = Math.floor(Math.random() * arr.length);

    while (num1 === num2) {
      num2 = Math.floor(Math.random() * arr.length);
    }

    const arrNum2 = arr[num2];
    arr[num2] = arr[num1];
    arr[num1] = arrNum2;

    for (let i = 1; i <= arr.length; i++) {
      arr[num1].style.left = `${5 * num1}px`;
      arr[num2].style.left = `${5 * num2}px`;
    }
  }
}

start();

startButton.addEventListener("click", () => {
  arr.sort();
  for (let i = 0; i < arr.length; i++) {
    arr[i].style.backgroundColor = "Red";
    setTimeout(() => {
      arr[i].style.left = `${5 * (arr[i].id - 1)}px`;
      arr[i].style.backgroundColor = "White";
    }, 5 * i);
  }
});

resetButton.addEventListener("click", start);
